import * as THREE from 'three';
import { OrbitControls } from '../../utils/orbitControls';

const SceneConstructor = (sceneConfig: SceneConfig) => {
  const { container: { width: canvasWidth, height: canvasHeight }, 
    colors: { light: lightColor, muted: mutedColor },
    helper: enableHelper
   } = sceneConfig
  // === scene ===
  const scene = new THREE.Scene()
  scene.background = new THREE.Color(lightColor)
  // scene.fog = new THREE.Fog(lightColor, 20, 100)

  // === renderer ===
  const renderer = new THREE.WebGLRenderer()
  renderer.setSize(canvasWidth, canvasHeight)
  renderer.setPixelRatio(window.devicePixelRatio)
  renderer.setClearColor(lightColor, 1.0)
  renderer.shadowMap.enabled = true
  renderer.shadowMap.type = THREE.PCFSoftShadowMap
  renderer.gammaInput = true
  renderer.gammaOutput = true

  // === camera ===
  const camera = new THREE.PerspectiveCamera(35, canvasWidth / canvasHeight, 0.1, 1000)
  camera.position.x = -10
  camera.position.z = -20
  camera.position.y = 50
  camera.lookAt(new THREE.Vector3(0, 0, 0))
  scene.add(camera);

  // === controls ===
  const controls = new OrbitControls(camera, renderer.domElement)
  controls.enableDamping = true
  controls.dampingFactor = 0.25
  controls.maxPolarAngle = Math.PI * 0.5

  // === main light ===
  let light = new THREE.HemisphereLight(0xffffff, 0xffffff, 1)
  scene.add(light)

  // === floor ===
  let floor = new THREE.Mesh(
    new THREE.PlaneBufferGeometry(100, 100),
    new THREE.MeshStandardMaterial({
        color: mutedColor
    })
  )
  floor.rotation.x = -Math.PI / 2
  floor.position.y = -0.5
  floor.position.z = 5
  floor.receiveShadow = true
  floor.name = 'floor'
  scene.add(floor)

  // const axisHelper = new THREE.AxisHelper(500);
  // scene.add(axisHelper);
  if (enableHelper) {
    const axesHelper = new THREE.AxesHelper( 100 );
    scene.add( axesHelper );
    const gridhelper = new THREE.GridHelper( 100, 20 );
    scene.add( gridhelper );
  }
  
  return {
    scene,
    renderer,
    camera,
    controls,
    light
  }
}

export default SceneConstructor;