import TWEEN from '@tweenjs/tween.js'
import * as THREE from 'three'

const l = console.log;
export const CameraTween = (controls: any, camera: THREE.PerspectiveCamera, from: Coord3D, to: Coord3D, lookAt: Coord3D,duration: number = 2000) => {
  // console.log(lookAt);
  // camera.up.set(0, 0, -1);
  // camera.lookAt(new THREE.Vector3(...Object.values(lookAt)))
  // camera.lookAt(...Object.values(lookAt))
  // controls.reset();
  l(to, lookAt);
  controls.target = new THREE.Vector3(lookAt.x, lookAt.y, lookAt.z)
  camera.lookAt(new THREE.Vector3(lookAt.x, lookAt.y, lookAt.z))
  new TWEEN.Tween(from)
    .to(to, duration)
    .easing(TWEEN.Easing.Linear.None)
    .onUpdate((res) => {
        camera.position.set(res.x, res.y, res.z)
        // camera.updateProjectionMatrix();
        // controls.update();
      })
      .start()
}

const PositionTween = (target: Coord3D, formula: Function, duration: number = 2000) => {
  new TWEEN.Tween(target)
    .to(formula(), duration)
    .easing(TWEEN.Easing.Exponential.InOut)
    .start()
}
export const LipstickTweens = {
  RGB: (target: Coord3D, color: THREE.Color, minGreen: number = 0,  border: number = 0, duration?:number) => {
    PositionTween(target, () => {
      return {
        x: Math.round(color.r * border) - border / 2,
        y: Math.round((color.g - minGreen) * border),
        z: Math.round(color.b * border) - border / 10
      }
    }, duration)
  },
  HSL: (target: Coord3D, color: THREE.HSL, minLight: number = 0,  border: number = 0, duration: number = 2000) => {
    const pi = Math.PI
    let {h, s, l} = color
    PositionTween(target, () => {
      return {
        x: Math.round(Math.sin(2 * pi * h) * s * border),
        y: Math.round((l - minLight) * border),
        z: Math.round(Math.cos(2 * pi * h) * s * border)
      }
    }, duration)
  },
  Product: (target: Coord3D, index: number, col: number, row: number, duration: number = 2000) => {
    PositionTween(target, () => {
      return {
        x: (index % col) * 2 - 5,
        y: 0,
        z: ((index / col >> 0) + row) * 2 + 1 - 5
      }
    }, duration)
  }
}

export const addDirectionLight = (scene, position: Coord3D, color = 0xffffff, intensity = 0.4) =>{
  let dirLight = new THREE.DirectionalLight(color, intensity)
  dirLight.position.set(position.x, position.y, position.z)
  dirLight.castShadow = true
  // let helper = new THREE.CameraHelper(dirLight.shadow.camera)
  // scene.add(helper)
  return dirLight
}