<div class="mode-control">
{#each modes as mode (mode)}
  <button class="mode-btn" on:click={e => handleModeChange(mode)}>{mode}</button>
{/each}
</div>

<script lang="ts">
  import { currentMode } from '../../stores/three.store'
  let modes = [
    'product',
    'rgb',
    'hsl'
  ]
  const handleModeChange = (mode) => {
    currentMode.update(() => mode)
  }
</script>

