

<div class="container" bind:this={container}></div>
<ModeControl class=".mode"/>
<BrandControl brands={brands} />

<script lang="ts">
  import { onMount, onDestroy } from 'svelte';
  import * as THREE from 'three';
  import TWEEN from '@tweenjs/tween.js'
  import { currentMode, currentBrand } from '../stores/three.store'
  import ModeControl from '../components/three/ModeControl.svelte';
  import BrandControl from '../components/three/BrandControl.svelte';
  import SceneConstructor from '../components/three/SceneConstructor.ts'
  import { addDirectionLight, LipstickTweens, CameraTween } from '../utils/threeHelper.ts'
  import { minMaxContainer } from '../utils/common.ts'
  import lipstickData from '../data/data.json'
  const l = console.log;
  const brands = lipstickData.data.map(e => e.brand);
  // data & tween
  let lipsticks: Lipstick[] = lipstickData.data[0].data
  l('init data: ', lipsticks);
  let productPanelCenter = { x: 0, z: 0 }
  let rgbCuboidBorder = { r: new minMaxContainer(), g: new minMaxContainer(), b: new minMaxContainer() }
  let rgbCuboidCenter = {}
  let hslCuboidBorder = { h: new minMaxContainer(), s: new minMaxContainer(), l: new minMaxContainer() }
  let hslCuboidCenter = {}

  let mode = 'product'
  let detect = false;
  let isFirstTween = true;
  let minG, minL, maxG, maxL;
  let column = 6
  // intereact
  let mouseCoord = new THREE.Vector2(0,0);
  const defaultCameraPos = {
    x: -10,
    y: 20,
    z: -20
  }
  let cubes: THREE.Mesh[] = [];
  let border = 30
  // raycaster
  let intersected;
  let cacheIntersectedColor;
  let focused;
  // colors
  const lightColor = '#eeeeee' // #c8e2e2
  const mutedColor = '#eeeeee' // #75c9be
  const intersectColor = '#ff0000'
  // container
  let container
  let canvasWidth = 800;
  let canvasHeight = 600;

  const { scene, renderer, camera, controls } = SceneConstructor({
    container: { width: canvasWidth, height: canvasHeight },
    colors: { light: lightColor, muted: mutedColor },
    helper: false
  })

  const clearScene = () => {
    // let arr = ['Mesh', 'DirectionalLight']
    
    // scene.traverseVisible(function(child) {
    //   if (arr.indexOf(child.type) > -1) {
    //       scene.remove(child);
    //   }
    // });
    let arr = ['Mesh', 'DirectionalLight']
    let indices = []
    scene.children.map((obj, index) => {
      obj.type === 'DirectionalLight' && indices.push(index)
      obj.type === 'Mesh' && obj.toggle && indices.push(index) 
    })
    while (indices.length) {
      indices.sort().reverse().map(idx =>
        scene.remove(scene.children[idx])
      )
      indices = [];
      scene.children.map((obj, index) => {
        obj.type === 'DirectionalLight' && indices.push(index)
        obj.type === 'Mesh' && obj.toggle && indices.push(index) 
      })
    }
    // l(indices)
    // indices.sort().reverse().map(idx =>
    //     scene.remove(scene.children[idx])
    // )
    // l(scene.children);
    // indices = [];
    // scene.children.map((obj, index) => {
    //     arr.indexOf(obj.type) > -1 && indices.push(index)
    // })
    // l(indices);
  }
  const initModel = (src: Lipstick[]) => {
    if (!src || !src.length) return
    let row: number = 0
    let rows = []
    let greenValueArray: number[] = []
    let lightValueArray: number[] = []
    let xmin = 9999, xmax = 0,  zmin = 9999, zmax = 0
    src.map((ls, productIndex, productArray) => {
        ls.colors.map((color, colorIndex, colorArray) => {
            let geometry = new THREE.BoxGeometry(1, 1, 1)
            let material = new THREE.MeshBasicMaterial({ color: color.color_hex })
            let cube = new THREE.Mesh(geometry, material)
            cube.castShadow = true
            cube.receiveShadow = false
            cube.position.x = (colorIndex % column) * 2 - 5
            xmin = Math.min(cube.position.x, xmin);
            xmax = Math.max(cube.position.x, xmax);
            cube.position.z = ((colorIndex / column >> 0) + row) * 2 + 1 - 5
            zmin = Math.min(cube.position.z, zmin);
            zmax = Math.max(cube.position.z, zmax);
            cube.position.y = 0
            cube.name = productIndex + '-' + colorIndex + '-' + row
            cube.toggle = true
            if (productIndex % 2 && colorIndex === Math.floor(colorArray.length / 2)) {
                let dirLight = addDirectionLight(scene, {
                    x: (1 % column) * 20 - 5,
                    y: 30,
                    z: ((1 / column >> 0) + row) * 20 + 1 - 5
                })
                dirLight.target = cube
                scene.add(dirLight)
            }
            cubes.push(cube)
            scene.add(cube)
            // export green in rgb and light in hsl colors
            const c: THREE.Color = material.color
            for (let key in rgbCuboidBorder) {
              rgbCuboidBorder[key].push(c[key])
            }
            greenValueArray.push(c.g)
            let hslColor: THREE.HSL = {}
            c.getHSL(hslColor) // require target object
            for (let key in hslCuboidBorder) {
              hslCuboidBorder[key].push(hslColor[key])
            }
            lightValueArray.push(hslColor.l)
        })

        rows.push(row)
        row = row + ((ls.colors.length + 1) / column >> 0) + 1
    })
    productPanelCenter = {
      x: xmin + (xmax - xmin) / 2,
      z: zmin + (zmax - zmin) / 2
    }
    minG = Math.min(...greenValueArray)
    minL = Math.min(...lightValueArray)

    let [rmin, rmax] = rgbCuboidBorder.r.getBorder();
    let [gmin, gmax] = rgbCuboidBorder.g.getBorder();
    let [bmin, bmax] = rgbCuboidBorder.b.getBorder();
    rgbCuboidCenter = {
      x: ((rmax + rmin - 1) * border)/2,
      y: (gmax + gmin - minG) * border,
      z: (bmax + bmin) * border /2 - border/5
    }
    const pi = Math.PI
    let [hmin, hmax] = hslCuboidBorder.h.getBorder();
    let [smin, smax] = hslCuboidBorder.s.getBorder();
    let [lmin, lmax] = hslCuboidBorder.l.getBorder();
    hslCuboidCenter = {
      x: (Math.sin(2 * pi * hmax) + Math.sin(2 * pi * hmin)) * border / 2
    }
    // l(hslCuboidCenter);
  }

  const raycaster = new THREE.Raycaster();
  const raycasterHandler = (mouseCoord) => {
    // update the picking ray with the camera and mouse position
    raycaster.setFromCamera(mouseCoord, camera)
    // calculate objects which intersecting with the picking ray
    let intersections = raycaster.intersectObjects(cubes)
    if (intersections.length > 0) {
        if (intersections[0].object.name !== 'floor') {
            if (intersected !== intersections[0].object) {
                // if (intersected) 
                // intersected.material.color.setHex(cacheIntersectedColor)
                intersected = intersections[0].object
                focused = intersections[0].object.name
                // l('focus: ', focused)
                let [product, color, _] = focused.split('-')
                l('focus: ', lipsticks[product].colors[color])
                // cacheIntersectedColor = intersected.material.color.getHex()
                // intersected.material.color.setHex(intersectColor)
            }
            
            container.style.cursor = 'pointer'
        }
    } else if (intersected) {
        // intersected.material.color.setHex(cacheIntersectedColor)
        intersected = null
        focused = null
        container.style.cursor = 'auto'
    }
  }
  const render = () => {
    renderer.render(scene, camera)
  }
  const animate = () => {
    requestAnimationFrame(animate)
    TWEEN.update()
    controls.update()
    render()
  }
  const onMouseClick = (e: MouseEvent) => {
    // if (detect) l(camera.position, camera.up, camera.lookAt);
      // e.preventDefault()
      // mouseCoord.x = (e.offsetX / canvasWidth) * 2 - 1
      // mouseCoord.y = -(e.offsetY / canvasHeight) * 2 + 1
      // raycasterHandler(mouseCoord)
  }
  const canvasSizeFit = () => {
    let { offsetWidth: width, offsetHeight: height } = container;
    camera.aspect = width / height
    camera.updateProjectionMatrix()
    renderer.setSize(width, height)
  }
  currentBrand.subscribe(value => {
    if (value < 0) return;
    clearScene();
    const newData = lipstickData.data[value].data;
    // l('changed brand: ', brands[value], newData);
    initModel(newData);
	});
  onMount(() => {
    // container.addEventListener('mousedown', ()=> { detect = true}, false)
    // container.addEventListener('mouseup', ()=> { detect = false}, false)
    // container.addEventListener('mousemove', onMouseClick, false)
    canvasSizeFit();
    initModel(lipsticks);
    container.appendChild(renderer.domElement)
    window.addEventListener('resize', canvasSizeFit, false)
    animate();
    // render();
  })
  onDestroy(() => {
    unsubscribe();
    container.removeEventListener('mousemove', onMouseClick, false)
    window.removeEventListener('resize', canvasSizeFit, false)
  })
  const tweenLayout = (type: string, duration: number) => {
    TWEEN.removeAll()
    // lipstick cell tweens
    for (let i = 0; i < cubes.length; i++) {
        let object = cubes[i]
        const color = object.material.color
        let hslColor = {h:null,s:null,l:null}
        color.getHSL(hslColor) // getHSL() require target object
        switch (type) {
            case 'rgb':{
              // x - r, y - g, z(linear) - b 
              LipstickTweens.RGB(object.position, color, minG, border)
              break;
            }
            case 'hsl': {
              // x - sin(2π*hue) * satur, y - light, z - cos(2π*hue) * satur (triangular)
              LipstickTweens.HSL(object.position, hslColor, minL, border)
              break
            }
            case 'product': {
              // x,z - prodcut and color index, y - 0 (linear)
              let colorIndex = object.name.split('-')[1] - 0
              let row = object.name.split('-')[2] - 0
              LipstickTweens.Product(object.position, colorIndex, column, row)
              break;
            }
        }
    }
    // camera tweens
    let from = {
      x: camera.position.x,
      y: camera.position.y,
      z: camera.position.z
    }
    switch (type) {
      case 'product': {
        // l('product center: ', productPanelCenter)
        CameraTween(
          controls,
          camera,
          from, 
          Object.assign({}, productPanelCenter, {y:50}),
          Object.assign({}, productPanelCenter, {y:0})
        )
        break;
      }
      case 'rgb': {
        CameraTween(
          controls,
          camera,
          from,
          Object.assign({}, rgbCuboidCenter, {y:10,z: -50}),
          Object.assign({}, rgbCuboidCenter, {y:10,z:0})
        )
        break;
      }
      case 'hsl': {
        CameraTween(
          controls,
          camera,
          from,
          Object.assign({}, hslCuboidCenter, {y:10,z: -40}),
          Object.assign({}, hslCuboidCenter, {y:10,z:0})
        )
      }
    }
  }
  const unsubscribe = currentMode.subscribe(value => {
    mode = value;
    if (isFirstTween) {
      isFirstTween = false
      return
    } 
    tweenLayout(value, 2000);
	});
</script>

<style>
  .container {
    z-index:1;
    position: absolute;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
  }
</style>