<div class="brand-control">
{#each brands as brand, index}
  <button class="brand-btn" on:click={e => handleModeChange(index)}>{brand}</button>
{/each}
</div>

<script lang="ts">
  import { currentBrand } from '../../stores/three.store'
  export let brands
  const handleModeChange = (i: number) => {
    // console.log('update brand: ', brand);
    currentBrand.update(() => i)
  }
</script>

